/* Dots */

.slick-dotted.slick-slider {
    margin-bottom: 30px;
}

.slick-dots {
    position: absolute;
    bottom: -25px;
    list-style: none;
    display: block;
    text-align: center;
    padding: 0;
    margin: 0;
    width: 100%;
    li {
        position: relative;
        display: inline-block;
        height: 10px;
        width: 10px;
        margin: 0 3px;
        padding: 0;
        cursor: pointer;
        button {
            border: 0;
            background: #A5A5A5;
            display: block;
            height: 10px;
            width: 10px;
            outline: none;
            line-height: 0px;
            font-size: 0px;
            color: transparent;
            padding: 5px;
            cursor: pointer;
            border: none;
            border-radius: 100%;

            &:hover, &:focus {
                outline: none;
                &:before {
                    opacity: 1.0;
                }
            }
            &:before {
                // position: absolute;
                // top: 0;
                // left: 0;
                // content: $slick-dot-character;
                // width: 20px;
                // height: 20px;
                // font-family: $slick-font-family;
                // font-size: $slick-dot-size;
                // line-height: 20px;
                // text-align: center;
                // color: #FFF100;

                // -webkit-font-smoothing: antialiased;
                // -moz-osx-font-smoothing: grayscale;
            }
        }
        &.slick-active button {
            width: 10px;
            height: 10px;
            padding: 4px;
            border: 1px solid #A5A5A5;
            background: unset;
            opacity: 1;
        }
    }
}

.slick-arrow {
    display: none !important;
}
